<template>
  <div class="root">
    <img class="back-icon" @click="goBack" :src="require('@/assets/images/back_icon.png')" alt="">
    <header>
      <img
        class="head-img"
        :src="require('@/assets/images/speaking_bg.png')"
        alt=""
      />
    </header>
    <main>
      <div class="main-wrap">
        <div class="gap-char-wrap flex-center">
          <img
            class="char-img"
            :src="require('@/assets/images/teacher_char.png')"
            alt=""
          />
        </div>
        <p class="loc-char">截止至{{ nowDate }}</p>
        <div class="list-item">
          <img
            class="bar-img"
            :src="require('@/assets/images/speaking_bar.png')"
            alt=""
          />
          <div class="message-box">
            <div class="gradient-wrap"></div>
            <TableList
              :showTitle="false"
              :columns="cloumnListPreach"
              :list="instructorListCmp"
            />
            <p>“亲师友”提供技术支持</p>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>
  
<script>
import moment from "moment";
export default {
  components: {
    TableList: () => import("@/components/TableList"),
  },
  computed: {
    // 授课老师排名列表
    instructorListCmp() {
      const arr = [];
      for (const key in this.instructorList) {
        arr.push({
          rankingName: `第${parseInt(key) + 1}名`,
          name: this.instructorList[key].instructor_info
            ? this.instructorList[key].instructor_info.name
            : "",
          visitorStr: `${
            this.instructorList[key].instructor_info
              ? this.instructorList[key].sum_visitor
              : 0
          }人`,
          roundStr: `${this.instructorList[key].host_time}场`,
        });
      }
      return arr;
    },
  },
  data() {
    return {
      // 列表数据标题以及数据 Key 值
      cloumnListPreach: [
        {
          label: "姓名",
          keyName: "name",
        },
        {
          label: "听课人数",
          keyName: "visitorStr",
        },
        {
          label: "场次",
          keyName: "roundStr",
        },
      ],
      // 授课老师排名列表
      instructorList: [],
      nowDate: "2022年10月21日",
    };
  },
  async created() {
    this.nowDate = moment().format("YYYY年MM月DD日");
    if (
      !this.$store.getters.associationInfo ||
      !this.$store.getters.associationInfo.id
    ) {
      await this.$store.dispatch("association/associationInfoDomainAPI");
    }
    this.instructorListAPI();
  },
  methods: {
    /**
     * 授课老师排名列表API
     */
    async instructorListAPI() {
      const aid = this.$store.getters.associationInfo.id;
      const data = { aid, limit: 50, page: 1 };
      const res = await this.$request("lecture.instructorPList", data);
      console.log("授课老师排名列表API_res :>> ", res);
      this.instructorList = res.data;
    },
    goBack() {
      this.$router.go(-1)
    }
  },
};
</script>

  
